import React from "react";
import "./Contact.scss";
import ContactForm from "./ContactFrom";

const Contact = () => {
  const handleClick = (event) => {
    event.preventDefault();
    const confirmed = window.confirm("");
    if (confirmed) {
      window.location.href = event.currentTarget.href;
    }
  };

  const handleCopyEmail = (event) => {
    event.preventDefault();
    const email = "houjinzhi@example.com";
    navigator.clipboard.writeText(email);
    window.alert(` ${email} `);
  };

  return (
    <div className="section">
      <div className="container">
        <div className="contact-left">
          <ContactForm />
        </div>
        <div className="contact-right">
          <h2 className="title"></h2>
          <div className="row">
            <div className="col">
              <a
                href="https://www.instagram.com/_houjinzhi_/"
                target="_blank"
                rel="noopener noreferrer"
                className="iconLink"
              >
                <i class="fa-brands fa-instagram"></i>
              </a>
            </div>
            <div className="col">
              <a href="#!" className="iconLink" onClick={handleCopyEmail}>
                <i className="fa-regular fa-envelope"></i>
              </a>
            </div>
            <div className="col">
              <a href="https://www.facebook.com/" className="iconLink">
                <i className="fa-brands fa-facebook-f"></i>
              </a>
            </div>
          </div>
          <div className="qrcode">
            <a
              href="https://portaly.cc/AUGANG.HJZ"
              target="_blank"
              rel="noopener noreferrer"
              onClick={handleClick}
            >
              <img src={process.env.PUBLIC_URL + "/QRcode.png"} alt="QRcode" />
            </a>
            <div className="hint">QRcode</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
